import { Navbar, Nav, Container } from 'react-bootstrap';
import React, { useEffect, useState, useContext } from 'react';
import { withRouter } from 'react-router';
import { NavLink } from 'react-router-dom';
import styled, { ThemeContext } from 'styled-components';
import endpoints from '../constants/endpoints';
import ThemeToggler from './ThemeToggler';

const styles = {
    logoStyle: {
        width: 50,
        height: 40,
    },
};

const ExternalNavLink = styled.a`
    color: ${(props) => props.theme.navbarTheme.linkColor};
    &:hover {
        color: ${(props) => props.theme.navbarTheme.linkHoverColor};
    }
    &::after {
        background-color: ${(props) => props.theme.accentColor};
    }
`;

const InternalNavLink = styled(NavLink)`
    color: ${(props) => props.theme.navbarTheme.linkColor};
    &:hover {
        color: ${(props) => props.theme.navbarTheme.linkHoverColor};
    }
    &::after {
        background-color: ${(props) => props.theme.accentColor};
    }
    &.navbar__link--active {
        color: ${(props) => props.theme.navbarTheme.linkActiveColor};
    }
`;

const NavBar = () => {
    const theme = useContext(ThemeContext);
    const [data, setData] = useState(null);
    const [expanded, setExpanded] = useState(false);

    useEffect(() => {
        fetch(endpoints.navbar, {
        method: 'GET',
        })
        .then((res) => res.json())
        .then((res) => setData(res))
        .catch((err) => err);
    }, []);

    return (
        <Navbar
        fixed="top"
        expand="md"
        bg="dark"
        variant="dark"
        className="navbar-custom"
        expanded={expanded}
        >
        <Container>
            {data?.logo && (
            <Navbar.Brand href="/">
                <img
                src={data?.logo?.source}
                className="d-inline-block align-top"
                alt="main logo"
                style={
                    data?.logo?.height && data?.logo?.width
                    ? { height: data?.logo?.height, width: data?.logo?.width }
                    : styles.logoStyle
                }
                />
            </Navbar.Brand>
            )}
            <Navbar.Toggle
            aria-controls="responsive-navbar-nav"
            onClick={() => setExpanded(!expanded)}
            />
            <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto" />
            <Nav>
                <InternalNavLink 
                    key='home'
                    onClick={() => {setExpanded(false) ; const section = document.querySelector('#home');
                    section.scrollIntoView( { behavior: 'smooth', block: 'start' } );}}
                    className="navbar__link"
                    to='#home'
                    theme={theme} 
                    >
                    Home
                </InternalNavLink>
                <InternalNavLink 
                    key='about'
                    onClick={() => {setExpanded(false) ; const section = document.querySelector('#about');
                    section.scrollIntoView( { behavior: 'smooth', block: 'start' } );}}
                    className="navbar__link"
                    to='#about'
                    theme={theme} 
                    >
                    About
                </InternalNavLink>
                <InternalNavLink 
                    key='skills'
                    onClick={() => {setExpanded(false) ; const section = document.querySelector('#skills');
                    section.scrollIntoView( { behavior: 'smooth', block: 'start' } );}}
                    className="navbar__link"
                    to='#skills'
                    theme={theme} 
                    >
                    Skills
                </InternalNavLink>
                <InternalNavLink 
                    key='education'
                    onClick={() => {setExpanded(false) ; const section = document.querySelector('#education');
                    section.scrollIntoView( { behavior: 'smooth', block: 'start' } );}}
                    className="navbar__link"
                    to='#education'
                    theme={theme} 
                    >
                    Education
                </InternalNavLink>
                <InternalNavLink 
                    key='experience'
                    onClick={() => {setExpanded(false) ; const section = document.querySelector('#experience');
                    section.scrollIntoView( { behavior: 'smooth', block: 'start' } );}}
                    className="navbar__link"
                    to='#experience'
                    theme={theme} 
                    >
                    Experience
                </InternalNavLink>
                <InternalNavLink 
                    key='projects'
                    onClick={() => {setExpanded(false) ; const section = document.querySelector('#projects');
                    section.scrollIntoView( { behavior: 'smooth', block: 'start' } );}}
                    className="navbar__link"
                    to='#projects'
                    theme={theme} 
                    >
                    Projects
                </InternalNavLink>
                <ExternalNavLink
                    key='resume'
                    href='https://drive.google.com/file/d/107DqOqSOmvce0s2fVogZ3PFgFT9y1W9I/view?usp=sharing'
                    target='_blank'
                    onClick={() => setExpanded(false)}
                    className="navbar__link"
                    theme={theme}
                    >
                Resume
                </ExternalNavLink>
            </Nav>
            <ThemeToggler
                onClick={() => setExpanded(false)}
            />
            </Navbar.Collapse>
        </Container>
        </Navbar>
    );
};

const NavBarWithRouter = withRouter(NavBar);
export default NavBarWithRouter;
